@import url("http://fonts.googleapis.com/css2?family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css?family=Nova Flat");
@import url("https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Spartan", sans-serif;
}

h1 {
  font-size: 50px;
  line-height: 64px;
  color: #222;
}

h2 {
  font-size: 46px;
  line-height: 54px;
  color: #222;
}

h4 {
  font-size: 20px;
  color: #222;
}

h6 {
  font-size: 12px;
  font-weight: 700;
}

p {
  font-size: 16px;
  color: #465b52;
  margin: 15px 0 20px 0;
}

.section-p1 {
  padding: 40px 80px;
}

.section-m1 {
  margin: 40px 0;
}

body {
  width: 100%;
}

/* Header */
#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 80px;
  background: #e3e6f3;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0);
}

#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

#navbar li {
  list-style: none;
  padding: 0 20px;
}

#navbar li a {
  text-decoration: none;
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a;
  transition: 0.3s ease;
}

#navbar li a:hover,
#navbar li a.active {
  color: orangered;
}

#navbar li a.active::after,
#navbar li a:hover::after {
  content: "";
  width: 6%;
  height: 2px;
  background: orangered;
  position: absolute;
  bottom: -4px;
  left: 20px;
}

.logo {
  height: 90px;
  align-items: flex-start;
  object-fit: cover;
}

.explore-by-state-section {
  font-size: 1em;
  width: 79%;
  margin-left: 8%;
  display: flexbox;
  background: rgba(33, 33, 33, 0.9);
  color: #f8f8f8;
  font-family: "Courier New", Courier, monospace;
  position: absolute;
  text-shadow: 0.1em 0.1em 0.05em #333;
  margin: 0 auto;
  margin-left: 10%;
  padding-left: 33%;
  padding-top: 5px;
  padding-bottom: 5px;
}

.explore-by-state-section .waviy {
  position: relative;
  -webkit-box-reflect: below -20px linear-gradient(transparent, rgba(0, 0, 0, 0.2));
  font-size: 28px;
}


.flip-container {
  display: flex;
  flex-direction: row;
  max-width: 79%;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-left: 10%;
  margin-top: 3%;
}

.flipper, .front,
.back {
  width: 320px;
  height: 427px;

  margin: 5px;
}

.front {
  z-index: 1.5;
  align-items: center;
  position: absolute;
}
.front .city {
  height: 427px;
  object-fit: cover;
  width: 320px;
}

.front .name {
  font-size: 2em;
  display: inline-block;
  background: rgba(33, 33, 33, 0.9);
  color: #f8f8f8;
  font-family: "Courier New", Courier, monospace;
  padding: 10px 10px;
  border-radius: 5px;
  bottom: 60px;
  right: 0%;
  position: absolute;
  text-shadow: 0.1em 0.1em 0.05em #333;

  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(-1deg);
}

.front .explore-button {
  position: relative;
  border-radius: 1px;
  display: inline-block;
  background: rgba(33, 33, 33, 0.9);
  color: #f8f8f8;
  text-align: center;
  font-family: nova flat;
  font-size: 28px;
  padding: 10px 10px;
  border-radius: 5px;
  bottom: 99%;
  left: 25%;
  transition: all 0.3s;
  cursor: pointer;
  opacity: 90%;
}

.explore-button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.3s;
}

.explore-button span:after {
  content: "\00bb\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.3s;
}

.explore-button:hover span {
  padding-right: 25px;
}

.explore-button:hover span:after {
  opacity: 1;
  right: -1;
}

